@font-face{
    font-family:"NanumGothic";
    font-weight: normal;
    src: url("../assets/NanumGothic.woff") format("woff");
}
body {
    font-family: "NanumGothic";
}

.back_container{
    position:relative;
    width: 100%;
    height: 100%;
}
.back_upper{
    width: 80%;
    margin-left:10%;
    height: 5px;
    background-color:#e5b0b2;
}

@media(min-width:768px)
{
    .back_upper{
        margin: 3% 10% 0 10%;
        width: 80%;
        height: 5px;
    }
}

.title{
    text-align: center;
    margin-top: 3%;
    font-size: 18pt;
    font-weight: 700;
    margin: 2% 10% 0 10%;
}

.box_name{
    font-size: 25pt;
    font-weight: 700;
    padding: 60px 0 0 100px;
}

.box_position{
    font-size: 13pt;
    color: #555;
    padding: 10px 0 0 100px;
}

.box_email{
    position: absolute;
    font-size: 18pt;
    right: 110px;
    font-weight: 700;
    top: 55px;
}

.box_git{
    position: absolute;
    font-size: 13pt;
    right: 110px;
    top: 95px;
}

.box_blog{
    position: absolute;
    font-size: 13pt;
    right: 110px;
    top: 115px;
}

.main_box{
    position:absolute; 
    left: 10%;
    background-color: #f2e9e5;
    width: 80%;
    height: 300px;
}

.section_first{
    width:100%;
    height: 450px;
    position: relative;
}

@media(min-width:768px)
{
    .main_box{
        position:absolute; 
        top: 80px;
        left: 10%;
        background-color: 	#d0c5c0B3;
        width: 80%;
        height: 180px;
    }
}

.trapezoid {
    position:absolute; 
    left:50%; 
    transform:translateX(-50%);
    top: 40px;
    width: 300px;
    height: 300px; 
    border-radius: 70%;
    overflow: hidden;
    border: white 6px solid;
}

.section_second{
    position: relative;
    margin: 0 10% 5% 10%;
    width: 80%;
}

.section_title{
    margin: 30px 0 30px 0;
    font-size: 18pt;
    font-weight: 700;
    color: #c87e8a;
}

.section_text2{
    margin: 10px 0 0 0;
}
.section_mid_title{
    margin: 30px 0 10px 0;
    font-size: 14pt;
    font-weight: 700;
}
.section_strong{
    color: #827b78;
    font-size: 14pt;
    margin: 5px 0 25px 0;
}

.menu{
    background-color: #f4f4f4;
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: inherit;
    text-align: left;
    border: none;
}

.menu:before{
    display: inline-block;
    content: '';
    height: 10px;
    width: 10px;
    margin-right: 12px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    transform: rotate(-45deg);
}

.menu[aria-expanded='true']::before, .menu[aria-selected='true']::before {
    transform: rotate(45deg);
}